<template>
    <div class="bs-examine">
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="审核中" name="first"></el-tab-pane>
            <el-tab-pane label="已通过" name="second"></el-tab-pane>
        </el-tabs>
        
        <el-table :data="list" style="width: 100%" max-height="250">
        <el-table-column fixed prop="pub_date" label="发表时间" width="200"></el-table-column>
        <el-table-column prop="article_book_name" label="所属大纲" width="200"></el-table-column>
        <el-table-column prop="title" label="文章标题" width="150"></el-table-column>
        <el-table-column prop="article_status" label="状态" width="120"></el-table-column>
        <el-table-column prop="content" label="内容" width="800"></el-table-column>
        <el-table-column fixed="right" label="操作" width="380">
            <template slot-scope="scope">
            <el-button
                @click.native.prevent="deleteRow(scope.$index, list)"
                type="text"
                size="small"
            >删除</el-button>
            <el-button
                @click.native.prevent="goToArticle(scope.$index, list)"
                type="text"
                size="small"
            >查看</el-button>
            <el-button
                @click.native.prevent="adoptExamine(scope.$index, list)"
                type="text"
                size="small"
                v-show="!isPass"
            >通过审核</el-button>
            <el-button
                @click.native.prevent="adoptExamine(scope.$index, list)"
                type="text"
                size="small"
                v-show="isPass"
            >文章封禁</el-button>
            </template>
        </el-table-column>
        </el-table>
    </div>
</template>

<script>
import * as ajax from "@/api";

export default {
    created(){
        ajax.getExamineList(1).then(resp=>{
            if(resp.data.code){
                this.list = resp.data.data.article_list
            }
        })
    },
    data() {
      return {
        activeName: 'first',
        list:[],
        isPass: false
      };
    },
    methods: {
      handleClick(tab, event) {
        let name = tab.$vnode.componentInstance.name
        if(name == 'second'){
            this.isPass = true
            ajax.getPassExamineList(1,true).then(resp=>{
                if(resp.data.code){
                    this.list = resp.data.data.article_list
                }
            })
        }else{
            this.isPass = false
            ajax.getExamineList(1).then(resp=>{
                if(resp.data.code){
                    this.list = resp.data.data.article_list
                }
            })
        }
      },
      adoptExamine(index,list){
          var status = ''
          list[index]["status_id"]==1 ? status= 2 : status= 1
          ajax.modifyArticleStatus(list[index]["id"],status).then(resp=>{
              if(resp.data.code){
                  list.splice(index, 1);
              }
          })
      },
      deleteRow(index,list){
          ajax.deleteArticle(list[index].id).then(resp=>{
              if(confirm('确定要删除吗？')){
                    if(resp.data.code){
                    list.splice(index, 1);
                    this.$notify({
                        title: '成功',
                        message: '这是一条成功的提示消息',
                        type: 'success'
                        });
                    }
              }
          })
      },
      goToArticle(index,list){
          this.$router.push({path:`/home/article`,query:{data: list[index]}})
      }
    }
}
</script>

<style lang='scss'>
   .bs-examine{
       flex: 1;
       padding-left: 30px;
        .el-tabs__item{
            padding-left: 20px;
       }
       .el-table {
            .cell {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            }
            .el-table__body-wrapper {
            overflow-y: hidden;
            }
            max-height: 100% !important;
            .el-table__body-wrapper {
            max-height: 100% !important;
            }
        }
        .el-table__fixed-body-wrapper{
            max-height: 100% !important;
        }
   }
</style>